.auth-form__submit-button {
  width: 100%;
  padding: 14px 84px;
  border: none;
  border-radius: 2px;
  background-color: #fff;
  color: #000;
  font-size: 18px;
  line-height: 24px;
  cursor: pointer;
  display: block;
}

@media screen and (max-width: 620px) {
  .auth-form__submit-button {
    font-size: 16px;
    line-height: 19px;
  }
}